import { Dialog, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Adds from './Adds';
import AlphabetTable from './AlphabetTable';
import { getParams } from './functions';
import Language from './Language';
import Legend from './Legend';
import Letters from './Letters';



const Alphabet = () => {
    const rus = useSelector(state => state.rus);
    const upcase = useSelector(state => state.upcase);
    const currentLang = useSelector(state => state.currentLang);

    const dispatch = useDispatch();


    const subtitles = {
        titlea: ['Alphabet', ''],
        titled: ['Dialect graphemes', ' '],
        titleh: ['Historical graphemes', ' '],
        titlel: ['Lexicographic graphemes', '   '],
        titleb: ['Character set', '   ']
    }

    const [allsigns, setAllSigns] = useState([]);
    const [ualpahabet, setUAlpahabet] = useState([]);
    const [lalpahabet, setLAlpahabet] = useState([]);
    const [udialect, setUDialect] = useState([]);
    const [ldialect, setLDialect] = useState([]);
    const [uhistoric, setUHistoric] = useState([]);
    const [lhistoric, setLHistoric] = useState([]);
    const [ulexic, setULexic] = useState([]);
    const [llexic, setLLexic] = useState([]);
    const [uunicodegroups, setUUnicodeGroups] = useState([]);
    const [lunicodegroups, setLUnicodeGroups] = useState([]);
    const [language, setLanguage] = useState({});
    const [langnames, setLangnames] = useState([]);
    const [langgroups, setLanggroups] = useState([]);
    // const { currentLang } = useParams();


    useEffect(() => {
        let uilang = rus ? rus : 0;
        if (getParams('ui') == 'ru') uilang = 1
        else if (getParams('ui') == 'en') uilang = 0

        let curlng = currentLang ? currentLang : (getParams('lang') ? getParams('lang') : 'Abazin')

        let lng = 'https://raw.githubusercontent.com/paratype/paratype.github.io/main/cyrillic-languages/site/baselib/' + curlng + '.json';
        fetch(lng)
            .then(res => res.json())
            .then(data => {
                // console.log(data);
                setAllSigns(data);
                setUAlpahabet(data.uppercase_alphabet);
                setLAlpahabet(data.lowercase_alphabet);
                setUDialect(data.uppercase_dialect);
                setLDialect(data.lowercase_dialect);
                setUHistoric(data.uppercase_historic);
                setLHistoric(data.lowercase_historic);
                setULexic(data.uppercase_lexic);
                setLLexic(data.lowercase_lexic);
                setUUnicodeGroups(data.uppercase_unicodes_list);
                setLUnicodeGroups(data.lowercase_unicodes_list);
            })

        let lng1 = 'https://raw.githubusercontent.com/paratype/paratype.github.io/main/cyrillic-languages/library/' + curlng + '.json'
        fetch(lng1)
            .then(res => res.json())
            .then(data => {
                setLanguage(data);
                setLangnames(data.alt_names_eng);
                setLanggroups(data.language_group_eng);
                dispatch({ type: 'switchLang', payload: data.local });
                console.log('Switched to ' + data.local);
                dispatch({ type: 'toggleRuEn', payload: uilang });
                window.history.pushState(null, null, '?lang=' + curlng + '&ui=' + (uilang ? 'ru' : 'en'));
            })

    }, [currentLang]);

    return (
        <>
            <Language language={language} langnames={langnames} langgroups={langgroups} />
            <Legend allsigns={allsigns} showtable />

            <div className={`lettersblock${upcase ? '' : ' hidden'}`}>
                <Letters alpahabet={ualpahabet} title={subtitles.titlea[rus]} />
                <Letters alpahabet={udialect} title={subtitles.titled[rus]} showUni={false} />
                <Letters alpahabet={uhistoric} title={subtitles.titleh[rus]} showUni={false} />
                <Letters alpahabet={ulexic} title={subtitles.titlel[rus]} showUni={true} />
                <Letters alpahabet={uunicodegroups} title={subtitles.titleb[rus]} showUni={true} />
            </div>

            <div className={`lettersblock${upcase ? ' hidden' : ''}`}>
                <Letters alpahabet={lalpahabet} title={subtitles.titlea[rus]} />
                <Letters alpahabet={ldialect} title={subtitles.titled[rus]} showUni={false} />
                <Letters alpahabet={lhistoric} title={subtitles.titleh[rus]} showUni={false} />
                <Letters alpahabet={llexic} title={subtitles.titlel[rus]} showUni={false} />
                <Letters alpahabet={lunicodegroups} title={subtitles.titleb[rus]} showUni={true} />
            </div>
        </>);
};

export default Alphabet;


