import { Routes, Route, BrowserRouter, Outlet, NavLink } from 'react-router-dom';
import React from 'react';
import Alphabet from './pages/Alphabet';
import './style/style.css';
import { Provider } from 'react-redux';
import { createStore } from 'redux';
import Langlist from './pages/Langlist';
import { persistStore, persistReducer } from "redux-persist";
import storage from 'redux-persist/lib/storage';
import PanCyrillic from './pages/PanCyrillic';

const initialState = {
  rus: 0,
  upcase: true,
  serif: false,
  italic: true,
  locallang: "CYR",
  currentLang: null
}

const persistConfig = {
  key: 'root',
  storage: storage,
  whitelist: ['upcase', 'serif', 'italic']
}

export const switchersReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'toggleRuEn': {
      // let ruen = null;
      // if (action.payload == 'ru') ruen = 1;
      // else if (action.payload == 'en') ruen = 0;
      // else ruen = (state.rus == 1) ? 0 : 1;
      return { ...state, rus: action.payload };
    }
    case 'toggleCase':
      return { ...state, upcase: !state.upcase };
    case 'toggleSerif':
      return { ...state, serif: !state.serif };
    case 'toggleItalic':
      return { ...state, italic: !state.italic };
    case 'switchLang':
      return { ...state, locallang: action.payload };
    case 'switchCurrentLang':
      return { ...state, currentLang: action.payload };
    default: return state
  }
}

const persistedReducer = persistReducer(
  persistConfig, switchersReducer
);

// export const store = createStore(switchersReducer);
export const store = createStore(persistedReducer);
export const persistor = persistStore(store);

function App() {
  return (
    <Provider store={store}>
      <div className='wrap'>
        <div className='langmenu'>
          <li><NavLink to="/">Alphabet</NavLink></li>
          <li><NavLink to="/pancyrillic">Pancyrillic table</NavLink></li>
          <p className='langlisttitle'>&nbsp;</p>
          <Langlist />
        </div>
        <div className='langmenufake'>&nbsp;</div>
        <div className='container'>
          {/* <Alphabet /> */}
          <Outlet />
          <Routes>
            <Route path="/" element={<Alphabet />} />
            <Route path="pancyrillic" element={<PanCyrillic />} />
          </Routes>
        </div>
      </div>
    </Provider>
  );
}

export default App;
