import React from 'react';
import SingleLetter from './SingleLetter';

const Letters = ({ alpahabet, title, showUni }) => {

    if (alpahabet[0]) {

        return (<>
            {title ? (<p className='smalltitle'>{title}</p>) : <></>}
            <div className='flexcontainer'>
                {alpahabet.map(item => {
                    if (item.alts && item.alts[0]) {

                        return (
                            <div className='altborder' key={item.id}><div className='altborder_in'>
                                <SingleLetter oneletter={item} showUni={showUni} />
                                {item.alts.map(itm => {
                                    return (<SingleLetter oneletter={itm} showUni={showUni} key={itm.id} />)
                                })}
                            </div></div>
                        )

                    } else {
                        return (<SingleLetter oneletter={item} showUni={showUni} key={item.id} />)
                    }

                })}
            </div>
        </>)

    }
    else {
        return (<></>)
    }

};

export default Letters;