import SingleLetter from "./SingleLetter";

const ShowTable = ({ signsupper, signslower, title, showUni }) => {

    const singleLetterMap = (array) => {
        console.log('Alts ', array);
        return (
            array.map(itm => {
                return (<tr><SingleLetter key={itm.id} oneletter={itm} showUni={showUni} tablemode={true} /></tr>)
            })
        )
    }

    if (signsupper[0] && signslower[0]) {
        let signsall = [];
        for (let i = 0; i < signsupper.length; i++) {
            let tmpup = signsupper[i] ? signsupper[i] : null;
            let tmplo = signslower[i] ? signslower[i] : null;
            signsall[i] = { upper: tmpup, lower: tmplo }
        }
        console.log('All ', signsall);
        return (<>
            <p className="tablestitle">{title}</p>
            <div className="tablewrap">
                <table><thead>
                    <tr>
                        <th colSpan={showUni ? 3 : 2}><p className="tableinnertitle">Uppercase</p></th>
                        <th colSpan={showUni ? 3 : 2}><p className="tableinnertitle">Lowercase</p></th>
                    </tr>
                    <tr>
                        <td className="letterchell">Phoneme</td>
                        <td className={`${showUni ? 'letterchell' : 'infochell'}`}>Unicodes</td>
                        {showUni ? (<td>Description</td>) : ''}
                        <td className="letterchell">Phoneme</td>
                        <td className={`${showUni ? 'letterchell' : 'infochell'}`}>Unicodes</td>
                        {showUni ? (<td>Description</td>) : ''}
                    </tr>
                </thead>
                    <tbody>{signsall.map(item => {
                        if ((item.upper?.alts && item.upper?.alts[0]) &&
                            (item.lower?.alts && item.lower?.alts[0])) {
                            return (
                                <tr>
                                    <td colSpan={2} className="containerrow">
                                        <table className="tablerow"><tbody>
                                            <tr>
                                                <SingleLetter key={item.upper.id} oneletter={item.upper} showUni={showUni} tablemode={true} />
                                            </tr>
                                            {singleLetterMap(item.upper.alts)}
                                        </tbody></table>
                                    </td>
                                    <td colSpan={2} className="containerrow">
                                        <table className="tablerow"><tbody>
                                            <tr>
                                                <SingleLetter oneletter={item.lower} showUni={showUni} tablemode={true} />
                                            </tr>
                                            {singleLetterMap(item.lower.alts)}
                                        </tbody></table>
                                    </td>
                                </tr>
                            )
                        }
                        else if ((item.upper?.alts && item.upper?.alts[0]) &&
                            !(item.lower?.alts && item.lower?.alts[0])) {
                            return (
                                <tr>
                                    <td colSpan={2} className="containerrow">
                                        <table className="tablerow"><tbody>
                                            <tr>
                                                <SingleLetter oneletter={item.upper} showUni={showUni} tablemode={true} />
                                            </tr>
                                            {singleLetterMap(item.upper.alts)}
                                        </tbody></table>
                                    </td>
                                    <SingleLetter key={item.lower.id} oneletter={item.lower} showUni={showUni} tablemode={true} />
                                </tr>
                            )
                        }
                        else if (!(item.upper?.alts && item.upper?.alts[0]) &&
                            (item.lower?.alts && item.lower?.alts[0])) {
                            return (
                                <tr>
                                    <SingleLetter key={item.upper.id} oneletter={item.upper} showUni={showUni} tablemode={true} />
                                    <td colSpan={2} className="containerrow">
                                        <table className="tablerow"><tbody>
                                            <tr>
                                                <SingleLetter oneletter={item.lower} showUni={showUni} tablemode={true} />
                                            </tr>
                                            {singleLetterMap(item.lower.alts)}
                                        </tbody></table>
                                    </td>
                                </tr>
                            )
                        }
                        else {
                            return (<tr>
                                {item.upper ? <SingleLetter oneletter={item.upper} showUni={showUni} key={item.upper.id} tablemode={true} /> : <></>}
                                {item.lower ? <SingleLetter oneletter={item.lower} showUni={showUni} key={item.lower.id} tablemode={true} /> : <></>}
                            </tr>)
                        };
                    })}</tbody>
                </table>
            </div>
        </>)
    }
    else return (<></>)
};

export default ShowTable;
