import { useSelector } from "react-redux";

const ToggleButtons = ({ titles, switcher, handleClick }) => {
    const rus = useSelector(state => state.rus);

    return (<>
        <button className={`togglebtns${switcher ? ' togglebtnsactive' : ''}`} onClick={handleClick}>{titles[rus]}</button>
        <button className={`togglebtns${switcher ? '' : ' togglebtnsactive'}`} onClick={handleClick}>{titles[rus + 2]}</button>
    </>)

};

export default ToggleButtons;