import React from 'react';
import Tooltip from '@mui/material/Tooltip';
import { useSelector } from 'react-redux';
import { getTypes } from './functions';

const SingleLetterPancyrillic = ({ oneletter }) => {
    const serif = useSelector(state => state.serif);
    const italic = useSelector(state => state.italic);
    const currentLang = useSelector(state => state.currentLang);
    const locallang = useSelector(state => state.locallang);

    const langtypes = () => {
        let langid = oneletter.languages.find(lang => lang.name === currentLang);
        let tmp = '';
        if (langid?.types && langid?.types[0]) {
            // console.log(langid.types);
            tmp = getTypes(langid.types);
        }
        return tmp
    }

    const bgcolor = () => {
        let langid = oneletter.languages.find(lang => lang.name === currentLang);
        // console.log(langid);
        let bg = langid ? " selectedsign" : ' unselectedsign';
        let letter = langid ? " selectedletter" : ' unselectedletter';
        let uni = langid ? " smalltitle smallunititle" : ' unselecteduni';
        return { bg, letter, uni }
    }

    const mainStyle = () => {
        let addstyle = "";
        if (serif && !italic) addstyle = " bigletterserifitalic";
        if (!serif && !italic) addstyle = " bigletteritalic";
        if (serif && italic) addstyle = " bigletterserif";
        return "bigletter" + addstyle + bgcolor().letter;
    }

    const showUnicode = () => {
        return (<p className={`smalltitle smallunititle${bgcolor().uni}`}>{oneletter.display_unicode}</p>)
    }

    const showLetter = () => {
        return (String.fromCharCode(`0x${oneletter.unicodes[0]}`))
    }

    // console.log(locallang);
    return (
        <Tooltip title={oneletter.description} followCursor>
            <div className={"letter " + bgcolor().bg}>
                {showUnicode()}
                <p className={mainStyle()}><span lang={oneletter.local}>{showLetter()}</span></p>
            </div>
        </Tooltip >)
};

export default SingleLetterPancyrillic;
