import { Avatar, Collapse, Dialog, DialogContent, Divider, IconButton, Menu } from '@mui/material';
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import AlphabetTable from './AlphabetTable';
import ToggleButtons from './ToggleButtons';

const Legend = ({ allsigns, showtable, showhelp }) => {
    const rus = useSelector(state => state.rus);
    const upcase = useSelector(state => state.upcase);
    const serif = useSelector(state => state.serif);
    const italic = useSelector(state => state.italic);

    const dispatch = useDispatch();

    const handleChangeCase = () => {
        dispatch({ type: 'toggleCase' });
    };

    const handleChangeSerif = () => {
        dispatch({ type: 'toggleSerif' });
    };

    const handleChangeItalic = () => {
        dispatch({ type: 'toggleItalic' });
    };
    const subtitles = {
        titlebtnCase: ['Upper', '', 'Lower', ''],
        titlebtnSerif: ['Serif', '', 'Sans', ''],
        titlebtnItalic: ['Normal', '', 'Italic', '']
    };

    // const [anchorEl, setAnchorEl] = useState(null);
    // const open = Boolean(anchorEl);
    // const handleClick = (event) => {
    //     setAnchorEl(event.currentTarget);
    // };
    // const handleClose = () => {
    //     setAnchorEl(null);
    // };

    const [openinfo, setOpenInfo] = useState(false);
    const handleInfoOpen = () => {
        setOpenInfo(!openinfo);
    };

    const [opendialog, setOpenDialog] = useState(false);
    const handleClickOpen = () => {
        setOpenDialog(!opendialog);
    };

    return (<>
        <div className='legendbgcontainer'>
            <div className='legendbg'>
                <div className='legenbtnswrap'>
                    <div className='legenbtns legenbtnsright'><ToggleButtons titles={subtitles.titlebtnSerif} switcher={serif} handleClick={handleChangeSerif} /></div>
                    <div className='legenbtns legenbtnsright'><ToggleButtons titles={subtitles.titlebtnCase} switcher={upcase} handleClick={handleChangeCase} /></div>
                    <div className='legenbtns'><ToggleButtons titles={subtitles.titlebtnItalic} switcher={italic} handleClick={handleChangeItalic} /></div>
                    {showtable ?
                        <>
                            <hr className='legenddiv' /><div className='legenbtns'>
                                <button className='togglebtns' onClick={handleClickOpen}>Show as text table</button></div>
                        </> : <></>
                    }
                </div>
                {showhelp ?
                    <>
                        <hr className='legenddiv' />
                        <IconButton onClick={handleInfoOpen} size="small"
                            aria-controls={openinfo ? 'account-menu' : undefined}
                            aria-haspopup="true"
                            aria-expanded={openinfo ? 'true' : undefined}
                        >
                            <Avatar sx={{ width: 27, height: 27, bgcolor: '#CEE2E7' }}>?</Avatar>
                        </IconButton>
                    </> : <></>
                }
            </div>
            <Collapse in={openinfo}>
                <div className='legendinfoopener'>
                    <div className='legendinfocontainer' >
                        <div className='legendinfowrap60'>
                            <p className='legendinfotext legengtitle'>Definitions:</p>
                            <p className='legendinfotext'>
                                <b>Grapheme</b>  a&nbsp;letter (including up to&nbsp;four-letter composites) or&nbsp;other graphic symbol used to&nbsp;write a&nbsp;language.
                            </p>
                            <p className='legendinfotext'>
                                <b>Phoneme</b>  a unit of a&nbsp;language phonetic system that represents basic speech sounds.
                            </p>
                            <p className='legendinfotext'>
                                <b>Alphabet</b>  an ordered set of&nbsp;graphemes that correspond to&nbsp;language phonemes.
                            </p>
                            <p className='legendinfotext legengtitle'>Non-standard Unicode code points:</p><p className='legendinfotext'>
                                Unicode values starting from F are&nbsp;assigned to&nbsp;characters outside of&nbsp;any standard block. These code point follow an&nbsp;informal convention accepted by&nbsp;several font manufacturers, including Paratype.</p>
                        </div>
                        <div className='legendinfowrap40'>
                            <p className='legendinfotext legengtitle'>Tags:</p><p className='legendinfotext'>
                                An alphabet element (grapheme) might have several variants of&nbsp;graphical representation. Such alternative graphemes do&nbsp;not change sort order and&nbsp;meaning of&nbsp;words they are used&nbsp;in. Graphemes within a&nbsp;group of&nbsp;variants are tagged as&nbsp;follows:</p>
                            <li className='legenditem'><div className="altmarkinfo"><div className="altmark">equ</div></div><div className="altmarkinfotext">Equivalent (identical) phoneme</div></li>

                            <li className='legenditem'><div className="altmarkinfo"><div className="altmark">mod</div></div>
                                <div className="altmarkinfotext">Modified (different) phoneme</div></li>

                            <li className='legenditem'><div className="altmarkinfo"><div className="altmark">locl</div></div>
                                <div className="altmarkinfotext">Localized form (<a href="https://helpx.adobe.com/fonts/using/open-type-syntax.html#locl">OpenType feature&nbsp;locl</a>)</div></li>
                        </div>
                    </div></div>
            </Collapse>
        </div >
        <Dialog open={opendialog} onClose={handleClickOpen} scroll='paper' maxWidth='lg' >
            <div className="tablecontainer">
                {allsigns ? <AlphabetTable allsigns={allsigns} /> : <></>}
            </div>
        </Dialog>
    </>);
};

export default Legend;

