import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

const LanglistItem = ({ item }) => {
    const rus = useSelector(state => state.rus);
    const currentLang = useSelector(state => state.currentLang);
    const dispatch = useDispatch();

    const handleChangeCurrentLang = () => {
        dispatch({ type: 'switchCurrentLang', payload: item.name_eng });
        // dispatch({ type: 'switchLang', payload: item.local });
        // console.log(item);
    };

    return (
        <button className={`langlistbutton${(currentLang === item.name_eng) ? ' langlistbuttonactive' : ''}`} onClick={handleChangeCurrentLang}>{rus ? item.name_rus : item.name_eng}</button>
    )
};


const Langlist = () => {
    const [langlistnames, setLangListNames] = useState([]);

    const rus = useSelector(state => state.rus);

    const titlebtnRuEn = ['RUS', 'RUS', 'ENG', 'ENG'];
    const dispatch = useDispatch();

    const handleChangerus = () => {
        dispatch({ type: 'toggleRuEn' });
    };

    // https://raw.githubusercontent.com/typedev/cyrillic_lib/master/cyrillic-languages
    // https://raw.githubusercontent.com/paratype/paratype.github.io/main/cyrillic-languages

    useEffect(() => {
        let lng = 'https://raw.githubusercontent.com/paratype/paratype.github.io/main/cyrillic-languages/cyrillic_library.json'
        fetch(lng)
            .then(res => res.json())
            .then(data => {
                setLangListNames(data);
            })
    }, []);

    const showLangList = () => {
        let tmpnames = langlistnames;
        if (rus) return tmpnames.sort((a, b) => a.name_rus > b.name_rus ? 1 : -1);
        else return tmpnames.sort((a, b) => a.name_eng > b.name_eng ? 1 : -1);
    }

    return (
        <>
            {/* <ToggleButtons titles={titlebtnRuEn} switcher={rus} handleClick={handleChangerus} /> */}
            {showLangList().map(item => {
                return (
                    item.enable ?
                        <LanglistItem key={item.name_eng} item={item} /> :
                        <></>
                )
            })
            }
        </>
    );
};

export default Langlist;
