import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Legend from "./Legend";
import PanCyrillicLetters from "./PanCyrillicLetters";


const PanCyrillic = () => {
    const upcase = useSelector(state => state.upcase);

    const [alltable, setAllTable] = useState([]);

    useEffect(() => {
        let lng = 'https://raw.githubusercontent.com/paratype/paratype.github.io/main/cyrillic-languages/site/cyrillic_characters_lib.json';
        fetch(lng)
            .then(res => res.json())
            .then(data => {
                setAllTable(data);
            })
        // console.log('Data ', alltable);
        // console.log('Initial ', alltable.uppercase_unicodes_list);
    }, []);

    return (<>
        <p className='langtitle'>Pancyrillic Character set</p>
        <Legend />
        <p>&nbsp;</p>
        <div className={`lettersblock${upcase ? '' : ' hidden'}`}>
            <div className='flexcontainer'>
                <PanCyrillicLetters alpahabet={alltable.uppercase_unicodes_list} />
                <PanCyrillicLetters alpahabet={alltable.uppercase_puazone_list} />
                <PanCyrillicLetters alpahabet={alltable.uppercase_nonunicode_list} />
            </div>
        </div>
        <div className={`lettersblock${!upcase ? '' : ' hidden'}`}>
            <div className='flexcontainer'>
                <PanCyrillicLetters alpahabet={alltable.lowercase_unicodes_list} />
                <PanCyrillicLetters alpahabet={alltable.lowercase_puazone_list} />
                <PanCyrillicLetters alpahabet={alltable.lowercase_nonunicode_list} />
            </div>
        </div>

    </>)

};

export default PanCyrillic;