
export function getTypes(types, totalbg) {
    let color = "";
    let mark = null;
    let feature = null;
    if (types) {
        if (types.indexOf('notrussiansign') >= 0) { color = 'notrussiansign' }
        if (types.indexOf('dialectsign') >= 0) { color = 'dialectsign' }
        if (types.indexOf('historicsign') >= 0) { color = 'oldersign' }
        if (types.indexOf('lexicsign') >= 0) { color = 'lexicosign' }
        if (types.indexOf('equivalentsign') >= 0) { mark = 'mod'; color = 'equsign' }
        if (types.indexOf('alternatesign') >= 0) { mark = 'equ'; color = 'altsign' }
        if (types.indexOf('replacementsign') >= 0) { mark = 'equ'; color = 'repsign' }
        if (types.indexOf('featuresign') >= 0) { feature = 'featuresign'; mark = 'locl'; color = 'equsign' }
    }
    if (totalbg) color = totalbg;
    return { color, mark, feature }
}

export function getLang() {
    var s = window.location.search.match(new RegExp('language=([^&=]+)'));
    return s ? s[1] : 'Abkhazian';
}

export function getParams(name) {
    var s = window.location.search;
    s = s.match(new RegExp(name + '=([^&=]+)'));
    return s ? s[1] : false;
}